import React, { useContext, useEffect, useMemo, useRef } from "react"
import ReactDOM from "react-dom"
import useMap from "../hooks/useMap"
import { KakaoMapMarkerClustererContext } from "./MarkerClusterer"

export interface CustomOverlayMapProps {
  /**
   * CustomOverlay Contianer id  .
   */
  id?: string

  /**
   * CustomOverlay Contianer className  .
   */
  className?: string

  /**
   * CustomOverlay Contianer style  .
   */
  style?: React.CSSProperties

  /**
   *   
   */
  position: {
    lat: number
    lng: number
  }
  /**
   * true         .
   */
  clickable?: boolean

  /**
   *  x . 0_1   .  0.5
   */
  xAnchor?: number

  /**
   *  y . 0_1   .  0.5
   */
  yAnchor?: number

  /**
   *   z-index
   */
  zIndex?: number

  /**
   *        callback   
   */
  onCreate?: (customOverlay: kakao.maps.CustomOverlay) => void
}

/**
 * Map CustomOverlay     .
 * `onCreate`   `CustomOverlay`          .
 */
const CustomOverlayMap: React.FC<CustomOverlayMapProps> = ({
  id,
  className,
  style,
  position,
  children,
  clickable,
  xAnchor,
  yAnchor,
  zIndex,
  onCreate,
}) => {
  const markerCluster = useContext(KakaoMapMarkerClustererContext)

  const map = useMap(`CustomOverlayMap`)
  const container = useRef(document.createElement("div"))

  const overlayPosition = useMemo(() => {
    return new kakao.maps.LatLng(position.lat, position.lng)
  }, [position.lat, position.lng])

  const overlay = useMemo(() => {
    const KakaoCustomOverlay = new kakao.maps.CustomOverlay({
      clickable: clickable,
      xAnchor: xAnchor,
      yAnchor: yAnchor,
      zIndex: zIndex,
      position: overlayPosition,
      content: container.current,
    })

    return KakaoCustomOverlay

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clickable, xAnchor, yAnchor])

  useEffect(() => {
    if (!map) return

    if (markerCluster) {
      markerCluster.addMarker(overlay)
    } else {
      overlay.setMap(map)
    }

    return () => {
      if (markerCluster) {
        markerCluster.removeMarker(overlay)
      } else {
        overlay.setMap(null)
      }
    }
  }, [map, markerCluster, overlay])

  useEffect(() => {
    if (onCreate) onCreate(overlay)
  }, [overlay, onCreate])

  useEffect(() => {
    overlay.setPosition(overlayPosition)
  }, [overlay, overlayPosition])

  useEffect(() => {
    if (!zIndex) return
    overlay.setZIndex(zIndex)
  }, [overlay, zIndex])

  useEffect(() => {
    if (id) container.current.id = id
  }, [id])

  useEffect(() => {
    if (className) container.current.className = className
  }, [className])

  useEffect(() => {
    if (style) {
      for (const [key, value] of Object.entries(style)) {
        container.current.style[key] = value
      }
    }
  }, [style])

  return ReactDOM.createPortal(children, container.current)
}

export default CustomOverlayMap
